.item {
    border-bottom: 1px solid #e5e5e5;
}

.item:last-of-type {
    border-bottom: none;
}

.users {
    border-collapse: collapse;
}

.buttonsContainer {
    display: flex;
    justify-content: end;
    gap: 10px;
    margin-bottom: 10px;
}

.container {
    max-width: 600px;
}

.thead {
    background-color: #e5e5e5;
}

.nameCol {
    min-width: 300px;
    padding: 10px 0 10px;
}

.ageCol {
    min-width: 300px;
    padding: 10px 0px 10px;
}
